import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import PreDefinedLayouts from '@/components/application-layouts/PreDefinedLayouts'
import BaseLine from '@/components/application-layouts/pre-defined-layouts/BaseLine'
import BaseLineFlipped from '@/components/application-layouts/pre-defined-layouts/BaseLineFlipped'
import Centered from '@/components/application-layouts/pre-defined-layouts/Centered'
import Complex from '@/components/application-layouts/pre-defined-layouts/Complex'
import Dark from '@/components/application-layouts/pre-defined-layouts/Dark'
import GoogleContacts from '@/components/application-layouts/pre-defined-layouts/GoogleContacts'
import GoogleKeep from '@/components/application-layouts/pre-defined-layouts/GoogleKeep'
import GoogleYoutube from '@/components/application-layouts/pre-defined-layouts/GoogleYoutube'
import Grid from '@/components/application-layouts/Grid'
import GridList from '@/components/application-layouts/GridList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/pre-defined-layouts',
      name: 'PreDefinedLayouts',
      component: PreDefinedLayouts,
      children: [
        { path: '/baseline', name: 'BaseLine', component: BaseLine },
        { path: '/baseline-flipped', name: 'BaseLineFlipped', component: BaseLineFlipped },
        { path: '/centered', name: 'Centered', component: Centered },
        { path: '/complex', name: 'Complex', component: Complex },
        { path: '/dark', name: 'Dark', component: Dark },
        { path: '/google-contacts', name: 'GoogleContacts', component: GoogleContacts },
        { path: '/google-keep', name: 'GoogleKeep', component: GoogleKeep },
        { path: '/google-youtube', name: 'GoogleYoutube', component: GoogleYoutube }
      ]
    },
    {
      path: '/grid',
      name: 'Grid',
      component: Grid
    },
    {
      path: '/grid-list',
      name: 'GridList',
      component: GridList
    }
  ]
})



