<template>
  <div class="pre-defined-layouts">
    <router-view />
    <div class="menu">
      <h2>Pre-defined layouts</h2>
      <ul>
        <li v-for="layout in layouts" :key="layout.page">
          <router-link :to="layout.route">{{ layout.page }}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      layouts: [
        { route: { name: 'BaseLine' }, page: 'BaseLine' },
        { route: { name: 'BaseLineFlipped' }, page: 'BaseLine Flipped' },
        { route: { name: 'Centered' }, page: 'Centered' },
        { route: { name: 'Complex' }, page: 'Complex' },
        { route: { name: 'Dark' }, page: 'Dark' },
        { route: { name: 'GoogleContacts' }, page: 'GoogleContacts' },
        { route: { name: 'GoogleKeep' }, page: 'GoogleKeep' },
        { route: { name: 'GoogleYoutube' }, page: 'GoogleYoutube' }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pre-defined-layouts .menu {
  position: absolute;
  bottom: 60px;
  left: 50%;
  transform: translate(-50%, 0%);
}
h2 {
  font-weight: normal;
  color: #d26363;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



