<template>
  <div class="container">
    <v-layout>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-card-actions>
            <v-select label="Size" :items="items" v-model="size"></v-select>
            <v-spacer></v-spacer>
          </v-card-actions>
          <v-container fluid v-bind="{ [`grid-list-${size}`]: true }">
            <v-layout row wrap>
              <v-flex
                xs4
                v-for="n in 9"
                :key="n"
              >
                <v-card flat tile>
                  <v-card-media
                    :src="`https://unsplash.it/150/300?image=${Math.floor(Math.random() * 100) + 1}`"
                    height="150px"
                  >
                  </v-card-media>
                </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
    <br>
    <hr>
    <br>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-toolbar flat>
            <v-btn icon>
              <v-icon>arrow_back</v-icon>
            </v-btn>
            <v-toolbar-title>Albums</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon>
              <v-icon>search</v-icon>
            </v-btn>
          </v-toolbar>
          <v-subheader>May</v-subheader>
          <v-container fluid grid-list-sm>
            <v-layout row wrap>
              <v-flex xs4 v-for="i in 6" :key="i">
                <img class="image" :src="`https://randomuser.me/api/portraits/men/${i + 20}.jpg`" alt="lorem" width="100%" height="100%">
              </v-flex>
            </v-layout>
          </v-container>
          <v-subheader>June</v-subheader>
          <v-container fluid grid-list-sm>
            <v-layout row wrap>
              <v-flex xs4 v-for="i in 6" :key="i">
                <img class="image" :src="`https://randomuser.me/api/portraits/women/${i + 5}.jpg`" alt="lorem" width="100%" height="100%">
              </v-flex>
            </v-layout>
          </v-container>
          <v-footer class="mt-5"></v-footer>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
  export default {
    data: () => ({
      size: 'sm',
      items: [
        { text: 'Extra small (2px)', value: 'xs' },
        { text: 'Small (4px)', value: 'sm' },
        { text: 'Medium (8px)', value: 'md' },
        { text: 'Large (16px)', value: 'lg' },
        { text: 'Extra large (24px)', value: 'xl' }
      ]
    })
  }
</script>

<style>
  .input-group__input {
    border-bottom: 1px solid #333;
  }
  .menu__content {
    transform: translateX(-80%) !important;
    background-color: #fff !important;
    left: 0 !important;
    transition: 0ms;
  }
</style>



