<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Application Layouts</h2>
    <ul>
      <li v-for="layout in layouts" :key="layout.page">
        <router-link :to="layout.route">{{ layout.page }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Vuetify JS',
      layouts: [
        { route: { name: 'PreDefinedLayouts' }, page: 'Pre-defined layouts' },
        { route: { name: 'Grid' }, page: 'Grid' },
        { route: { name: 'GridList' }, page: 'GridList' }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



